	import cmdProgress from "@/components/cmd-progress/cmd-progress.vue"
export default {  
	    data() {
	    	return {
				listnum:"",//选择题总数据
	    		active: 1,//进度条
				molecule:1,//当前题数
				Denominator:0,//总题数
				choicechild:"",//最终选择内容
				indexColor:0,
				questionText:"",//当前题目
				selector:null,//当前选项
	    		list1: [{
	    			title: '已提交'
	    		}, {
	    			title: '已被接受'
	    		}]
	    	}
	    },  
	    components: {  
	        cmdProgress,  
	    },
		onLoad:function(){
			uni.request({
			    url: 'https://www.ccweb.xyz/gas/wx/api/getTopic', //
			    data: {},
			    success: (res) => {
					let item;
					this.Denominator=1;
					item=res.data.data[2];
					this.questionText=item.name;
					this.selector=item.option.split(";");
					// num=JSON.parse(item.option);
			        console.log(this.questionText);
					console.log(this.selector);
			    }
			});
			this.active=(this.molecule/this.Denominator)*100;
			
		},
		methods:{
			getTndex:function(indexx,e){
				this.indexColor=indexx;
				this.choicechild=e.currentTarget.dataset.ref;
				// return false;
			},
			// click:function(e){
			// 	console.log(e.currentTarget.dataset.ref);
			// },
			// 填空题 获取当前值
			inputText:function(e){
				this.choicechild=e.detail.value;
				// console.log(e.detail.value);
			},
			pre:function(){
				this.molecule--;
				if(this.molecule<1){
					this.molecule=1;
					uni.showModal({
					    title: '提示',
					    content: '即将返回到填空题页面，是否确认',
					    success: function (res) {
					        if (res.confirm) {
					            uni.navigateBack({
					            	delta:1,
					            })
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
				this.active=(this.molecule/this.Denominator)*100;
			},
			next:function(){
				if(this.choicechild!=""&&this.choicechild!=undefined){
				this.molecule++;
				if(this.molecule>this.Denominator){
					this.molecule=this.Denominator;
					uni.showToast({
					    title: '答题完毕，七天后查询成绩',
						icon:"none",
					    duration: 2000,
						
					});
					setTimeout(function(){ 
						uni.reLaunch({
						    url: '../company/pHome'
						}) 
						}, 2200)
				}
				this.active=(this.molecule/this.Denominator)*100;
				}else{
					uni.showToast({
					    title: '选项为空，请重新检查',
					    duration: 1000
					});
				}
			},
		}
		
	}
